// ==========================================================================
// Collection Helpers 
// ==========================================================================
@import "_helpers/_collection";

// ==========================================================================
// Base on Eric Meyer Css Reset
// ==========================================================================
// 
// Initialization all html tag default value
// 

div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    vertical-align: top;
  }
}

// Initialization tag
// ==========================================================================
// 
// HTML5 display-role reset for older browsers
// 

main,article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}

html, body{
  margin: 0;
  padding: 0;
}

// setting page default font siez
// font-size: 62.5%; -->  10 ÷ 16 × 100% = 62.5%
// 

html{
  font-size: 62.5%; 
  -ms-text-size-adjust: 100%; 
  -webkit-text-size-adjust: 100%;
}
body {
  @include font-size($fz-13);
  font-family: Arial, Helvetica, 'LiHei', "Microsoft JhengHei", "PMingLiU", "微軟正黑體", "新細明體", sans-serif;
  color: $gr-dark;
  line-height: 1.4;
}

ol, ul {
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: unset;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

hr {
  height: 0
}

// Embedded content
// ==========================================================================

img, embed, object {
  display: block;
  max-width: 100%;
  height: auto;
  border: 0
}

svg:not(:root) {
  overflow: hidden;
}

// Forms
// ==========================================================================

input, select {
  vertical-align: middle;
}

input, button, textarea, select, optgroup, option {
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  font-weight: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
}

input {
  &[type="submit"], &[type="radio"], &[type="checkbox"], &[type="button"], &[type="reset"] {
    cursor: pointer;
  }
}

input {
  &[type="radio"], &[type="checkbox"] {
    padding: 0;
  }
}

button, select {
  cursor: pointer;
}


// Links
// ==========================================================================

a {
  text-decoration: none;
  background-color: transparent;
  &:active, &:hover {
    outline: 0;
  }
}
// lists
// ==========================================================================

ul, ol {
  list-style: none;
}

// box-sizing
// ==========================================================================
// 
// clear all box-sizing setting
// 

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  &:before, &:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
}




